import { api } from "@/apiHandler/page";

export const dsaService = {
  // Get all topics with subtopics
  getTopics: async () => {
    return await api.get("/topics/all");
  },

  // Mark subtopic as completed
  markAsCompleted: async (subtopicId) => {
    return await api.post(`/subtopics/${subtopicId}/complete`);
  },

  // Remove from completed
  removeFromCompleted: async (subtopicId) => {
    return await api.delete(`/subtopics/${subtopicId}/complete`);
  },

  // Get completed subtopics
  getCompletedSubtopics: async () => {
    return await api.get("/subtopics/completed");
  },

  // Get subtopics by topic
  getSubtopicsByTopic: async (topicId) => {
    return await api.get(`/subtopics/topic/${topicId}`);
  }
};
