// src/components/DSA/ProgressTracker.js
"use client";

import { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  LinearProgress,
  Paper,
  useTheme,
  Divider,
  alpha,
  CircularProgress,
  Button,
  Tooltip,
  IconButton,
} from '@mui/material';
import { Refresh } from '@mui/icons-material';
import { dsaService } from '@/services/dsaService';

const ProgressItem = ({ label, value, total, color, theme }) => (
  <Box sx={{ mb: 2 }}>
    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5 }}>
      <Typography variant="body2" color="text.secondary">
        {label}
      </Typography>
      <Typography variant="body2" fontWeight={500} color={`${color}.main`}>
        {value}/{total}
      </Typography>
    </Box>
    <LinearProgress
      variant="determinate"
      value={total > 0 ? (value / total) * 100 : 0}
      sx={{
        height: 8,
        borderRadius: 4,
        backgroundColor: alpha(theme.palette[color].light, 0.2),
        '& .MuiLinearProgress-bar': {
          backgroundColor: theme.palette[color].main,
        },
      }}
    />
  </Box>
);

export default function DSAProgressTracker({ topics, completedSubtopicIds = [] }) {
  const theme = useTheme();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [completedSubtopics, setCompletedSubtopics] = useState(completedSubtopicIds);

  const calculateProgress = () => {
    if (!topics?.length) return null;

    let totalProblems = 0;
    let completedProblems = 0;
    let easyCount = 0;
    let mediumCount = 0;
    let hardCount = 0;
    let easyCompleted = 0;
    let mediumCompleted = 0;
    let hardCompleted = 0;

    topics.forEach((topic) => {
      topic.subtopics?.forEach((subtopic) => {
        const isCompleted = completedSubtopics.includes(subtopic._id);
        totalProblems++;
        
        if (isCompleted) {
          completedProblems++;
        }

        // Count by difficulty
        switch ((subtopic.difficulty || '').toLowerCase()) {
          case 'easy':
            easyCount++;
            if (isCompleted) easyCompleted++;
            break;
          case 'medium':
            mediumCount++;
            if (isCompleted) mediumCompleted++;
            break;
          case 'hard':
            hardCount++;
            if (isCompleted) hardCompleted++;
            break;
          default:
            // Default to medium if no difficulty is set
            mediumCount++;
            if (isCompleted) mediumCompleted++;
        }
      });
    });

    const progressPercentage =
      totalProblems > 0 ? Math.round((completedProblems / totalProblems) * 100) : 0;

    return {
      total: totalProblems,
      completed: completedProblems,
      percentage: progressPercentage,
      easy: {
        total: easyCount,
        completed: easyCompleted,
        percentage: easyCount > 0 ? Math.round((easyCompleted / easyCount) * 100) : 0,
      },
      medium: {
        total: mediumCount,
        completed: mediumCompleted,
        percentage: mediumCount > 0 ? Math.round((mediumCompleted / mediumCount) * 100) : 0,
      },
      hard: {
        total: hardCount,
        completed: hardCompleted,
        percentage: hardCount > 0 ? Math.round((hardCompleted / hardCount) * 100) : 0,
      },
    };
  };

  const refreshProgress = async () => {
    try {
      setLoading(true);
      const response = await dsaService.getCompletedSubtopics();
      setCompletedSubtopics(response.data?.map(item => item._id) || []);
      setError(null);
    } catch (err) {
      setError('Failed to refresh progress');
      console.error('Error refreshing progress:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    refreshProgress();
  }, [topics]);

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 3,
          borderRadius: 2,
          backgroundColor: theme.palette.background.paper,
          border: `1px solid ${theme.palette.divider}`,
          textAlign: 'center',
        }}
      >
        <Typography color="error" gutterBottom>
          {error}
        </Typography>
        <Button
          variant="outlined"
          startIcon={<Refresh />}
          onClick={refreshProgress}
          size="small"
        >
          Retry
        </Button>
      </Paper>
    );
  }

  const progressData = calculateProgress();
  if (!progressData) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 3,
          borderRadius: 2,
          backgroundColor: theme.palette.background.paper,
          border: `1px solid ${theme.palette.divider}`,
          textAlign: 'center',
        }}
      >
        <Typography>No data available</Typography>
      </Paper>
    );
  }

  return (
    <Paper
      elevation={0}
      sx={{
        p: 3,
        borderRadius: 2,
        backgroundColor: theme.palette.background.paper,
        border: `1px solid ${theme.palette.divider}`,
        position: 'relative',
      }}
    >
      <Tooltip title="Refresh progress">
        <IconButton
          onClick={refreshProgress}
          size="small"
          sx={{
            position: 'absolute',
            top: 8,
            right: 8,
          }}
          disabled={loading}
        >
          <Refresh fontSize="small" />
        </IconButton>
      </Tooltip>

      <Box sx={{ mb: 3, textAlign: 'center' }}>
        <Typography variant="h6" gutterBottom>
          Your DSA Progress
        </Typography>
        <Box sx={{ position: 'relative', display: 'inline-flex', mb: 2 }}>
          <CircularProgress
            variant={loading ? 'indeterminate' : 'determinate'}
            value={progressData.percentage}
            size={120}
            thickness={4}
            sx={{
              color: theme.palette.primary.main,
              '& .MuiCircularProgress-circle': {
                strokeLinecap: 'round',
              },
            }}
          />
          <Box
            sx={{
              top: 0,
              left: 0,
              bottom: 0,
              right: 0,
              position: 'absolute',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}
          >
            <Typography
              variant="h5"
              component="div"
              color="text.primary"
              fontWeight={600}
            >
              {loading ? '...' : `${progressData.percentage}%`}
            </Typography>
          </Box>
        </Box>
        <Typography variant="body2" color="text.secondary">
          {progressData.completed} of {progressData.total} problems solved
        </Typography>
      </Box>

      <Divider sx={{ my: 2 }} />

      <Box sx={{ mt: 3 }}>
        <Typography variant="subtitle2" gutterBottom>
          Progress by Difficulty
        </Typography>
        <ProgressItem
          label="Easy"
          value={progressData.easy.completed}
          total={progressData.easy.total}
          color="success"
          theme={theme}
        />
        <ProgressItem
          label="Medium"
          value={progressData.medium.completed}
          total={progressData.medium.total}
          color="warning"
          theme={theme}
        />
        <ProgressItem
          label="Hard"
          value={progressData.hard.completed}
          total={progressData.hard.total}
          color="error"
          theme={theme}
        />
      </Box>
    </Paper>
  );
}
